import { Grid, Typography } from "material-ui"
import { StyleRulesCallback, withStyles } from "material-ui/styles"
import * as React from "react"

import { Logo } from "../Logo"

type Props = {}

const footerAbout: React.SFC<Props & { classes: StyleClassNames }> = ({
  classes,
}) => (
  <Grid item xs={10} md={3} sm={5} className={classes.gridItem}>
    <Logo className={classes.logo} />
    <Typography
      type="body1"
      gutterBottom
      color="inherit"
      className={classes.vision}
    >
      Hallingdata hjelper bedrifter og organisasjoner med fornuftig bruk av
      informasjonsteknologi (IT). Vi er IT-eksperter som skjnner
      forretningsdrift. Ta kontakt for  finne ut hvordan vi kan hjelpe deg og
      din organisasjon.
    </Typography>
    <Typography type="body2" color="inherit">
      Besksadresse:
    </Typography>
    <Typography type="body1" gutterBottom color="inherit">
      Hallingdal Nringshage<br />
      Gamlevegen 6 <br />
      3550 GOL<br />
    </Typography>
  </Grid>
)
type StyleClassNames = {
  logo: string
  vision: string
  gridItem: string
}

const styles: StyleRulesCallback = theme => ({
  logo: {
    height: 38,
    marginBottom: 6,
  },
  vision: {
    marginBottom: 26,
  },
  gridItem: {
    marginBottom: 30,
  },
})

export const FooterAbout = withStyles(styles)<Props>(footerAbout)



